import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import Typography from "@material-ui/core/Typography";
import Slider from "@material-ui/core/Slider";
import Input from "@material-ui/core/Input";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Alert from "@material-ui/lab/Alert";
import { Button } from "@material-ui/core";
import Iframe from "react-iframe";

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  root: {
    textAlign: "center",
    width: "100%",
  },
  paper: {
    position: "relative",
    padding: theme.spacing(3),
    textAlign: "center",
    height: "100%",
  },
  paper1: {
    padding: theme.spacing(3),
    textAlign: "center",
  },
  button: {
    textAlign: "center",
  },
  input1: {
    width: "45px",
    marginLeft: "5px",
  },
  tleft: {
    textAlign: "left",
  },
  tright: {
    textAlign: "right",
    marginRight: "30px",
  },
  list: {
    width: 250,
  },
  fullList: {
    width: "auto",
  },
}));

export default function SimulatorTab3() {
  let loop = useRef(null);

  const values = {
    1: {
      coal: "lignite",
      ash: 5.9,
      moist: 38.4,
      vm: 27.1,
      fc: 28.6,
    },
    2: {
      coal: "subituminous",
      ash: 8.0,
      moist: 20.7,
      vm: 32.6,
      fc: 38.7,
    },
    3: {
      coal: "bituminous",
      ash: 12.1,
      moist: 2.7,
      vm: 32,
      fc: 53.2,
    },
    4: {
      coal: "anthracite",
      ash: 10.6,
      moist: 2.1,
      vm: 5.7,
      fc: 81.6,
    },
  };

  const classes = useStyles();
  const [type_of_coal, setCoal] = React.useState(1);
  const [wt_of_coal, setWeight] = React.useState(0.8);
  const [lab_oven_temp, setLTemp] = React.useState(110);
  const [furnace_temp, setFTemp] = React.useState(925);
  const [furnace_temp2, setFTemp2] = React.useState(750);
  const [wt_before_heating, setWBH] = React.useState(0);
  const [wt_after_heating, setWAH] = React.useState(0); //Same as moisture free coal I guess
  const [wt_after_removing_VM, setWVM] = React.useState(0);
  const [wt_with_ash, setWWA] = React.useState(0);
  const [sim, setSim] = React.useState(false);
  const [url, setUrl] = React.useState("");

  const handleCoalChange = (event) => {
    setCoal(event.target.value);
  };

  const handleWeightChange = (e) => {
    setWeight(e.target.value);
    console.log(wt_of_coal);
  };

  const SliderWTemp = (event, newValue) => {
    setWeight(newValue);
  };

  const SliderLTemp = (event, newValue) => {
    setLTemp(newValue);
  };

  const SliderFTemp = (event, newValue) => {
    setFTemp(newValue);
  };

  const handleLTemp = (event) => {
    setLTemp(event.target.value === "" ? "" : Number(event.target.value));
  };

  const handleFTemp = (event) => {
    setFTemp(event.target.value === "" ? "" : Number(event.target.value));
  };

  function valuetext_(value) {
    return `${value}*C`;
  }

  function valuetext_ltemp(value) {
    return `${value}*C`;
  }

  function valuetext_ftemp(value) {
    return `${value}*C`;
  }

  function calWt(val, per) {
    return val * (1 - per / 100);
  }

  useEffect(() => {
    setUrl(
      "/Proximate-Analysis/?wt_coal=" +
        wt_of_coal +
        "&type_coal=" +
        type_of_coal +
        `&oven_temp=110&furnace_temp=925&wt_before_heating=${
          wt_of_coal + 23.64
        }&wt_after_heating=${(
          23.64 + calWt(wt_of_coal, values[type_of_coal].moist)
        ).toFixed(4)}&wt_without_moisture=${(
          23.64 + calWt(wt_of_coal, values[type_of_coal].moist)
        ).toFixed(4)}&wt_without_vm=${(
          23.64 +
          calWt(
            wt_of_coal,
            values[type_of_coal].moist + values[type_of_coal].vm
          )
        ).toFixed(4)}&wt_ash=${(
          23.64 +
          calWt(
            wt_of_coal,
            values[type_of_coal].vm +
              values[type_of_coal].moist +
              values[type_of_coal].fc
          )
        ).toFixed(4)}&fur2=750`
    );
  }, [wt_of_coal, type_of_coal]);

  return (
    <>
      {!sim && (
        <Grid container spacing={1}>
          <Grid item xs={3}>
            <Paper className={classes.paper}>
              <Paper elevation={0}>
                <div>
                  <FormControl className={classes.formControl}>
                    <Typography gutterBottom>Type of Coal:</Typography>
                    <Select
                      labelId="demo-simple-select-label"
                      id="demo-simple-select"
                      defaultValue={1}
                      value={type_of_coal}
                      onChange={handleCoalChange}
                    >
                      <MenuItem value={1}>Lignite</MenuItem>
                      <MenuItem value={2}>Subbituminous</MenuItem>
                      <MenuItem value={3}>Bituminous</MenuItem>
                      <MenuItem value={4}>Anthracite</MenuItem>
                    </Select>
                  </FormControl>
                </div>
                <br />
                <br />
                <Typography gutterBottom>
                  Select weight of air dried powdered coal:
                </Typography>
                <Input
                  className={classes.input}
                  value={wt_of_coal}
                  margin="dense"
                  onChange={handleWeightChange}
                  inputProps={{
                    step: 0.1,
                    min: 0.8,
                    max: 1.2,
                    type: "number",
                    "aria-labelledby": "input-slider",
                  }}
                />
                g
                <br />
                <br />
                <Slider
                  aria-labelledby="discrete-slider"
                  valueLabelDisplay="auto"
                  onChange={SliderWTemp}
                  value={typeof wt_of_coal === "number" ? wt_of_coal : 1}
                  step={0.1}
                  marks
                  min={0.8}
                  max={1.2}
                />
                <br />
                <br />
                <Typography gutterBottom>
                  Weight of crucible: 23.64 g
                </Typography>
                <br />
                <br />
                <Typography gutterBottom>
                  Temperature of Laboratory Oven: <b>110&deg;C</b>
                </Typography>
                <br />
                <br />
                <Typography gutterBottom>
                  Temperature of Muffle Furnace: <br /> <b>925&deg;C</b>
                  <br />
                  (Determination of Volatile Matter)
                </Typography>
                <br />
                <br />
                <Typography gutterBottom>
                  Temperature of Muffle Furnace: <br /> <b>750&deg;C</b>
                  <br />
                  (Determination of Ash Content)
                </Typography>
                <br />
                <br />
              </Paper>
            </Paper>
          </Grid>
          <Grid item xs={9}>
            <Paper className={classes.paper}>
              <Paper elevation={0}>
                <img
                  src="/images/Proximate-Analysis.png"
                  width="95%"
                  className="mt-5"
                />
                <Button
                  className="ml-auto mr-auto mt-5"
                  id="start"
                  variant="outlined"
                  color="primary"
                  onClick={(e) => {
                    setSim(true);
                  }}
                >
                  Start
                </Button>

                <br />
                <br />
              </Paper>
            </Paper>
          </Grid>
        </Grid>
      )}
      {sim && (
        <>
          <Iframe
            url={url}
            width="100%"
            height="750px"
            id="myId"
            className="myClassname"
            display="initial"
            position="relative"
          />
          <Paper elevation={0} className="text-center">
            <Button
              className="ml-auto mr-auto my-3"
              id="start"
              style={{ textAlign: "center" }}
              variant="outlined"
              color="secondary"
              onClick={(e) => {
                setSim(false);
              }}
            >
              RESET
            </Button>
          </Paper>
        </>
      )}
    </>
  );
}
