import React, { useRef, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import Typography from "@material-ui/core/Typography";
import Slider from "@material-ui/core/Slider";
import Input from "@material-ui/core/Input";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Alert from "@material-ui/lab/Alert";
import { Button } from "@material-ui/core";
import Iframe from "react-iframe";

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  root: {
    textAlign: "center",
    width: "100%",
  },
  paper: {
    position: "relative",
    padding: theme.spacing(3),
    textAlign: "center",
    height: "100%",
  },
  paper1: {
    padding: theme.spacing(3),
    textAlign: "center",
  },
  button: {
    textAlign: "center",
  },
  input1: {
    width: "45px",
    marginLeft: "5px",
  },
  tleft: {
    textAlign: "left",
  },
  tright: {
    textAlign: "right",
    marginRight: "30px",
  },
  list: {
    width: 250,
  },
  fullList: {
    width: "auto",
  },
}));

export default function SimulatorTab6() {
  let loop = useRef(null);

  const classes = useStyles();
  const [vol_of_hcl, setWeight] = React.useState(50);
  const [normality_NaOH, setLTemp] = React.useState(0.01);
  const [sim, setSim] = React.useState(false);
  const [url, setUrl] = React.useState("");

  const handleV1Change = (e) => {
    setWeight(e.target.value);
    console.log(vol_of_hcl);
  };

  const SliderV1 = (event, newValue) => {
    setWeight(newValue);
  };

  const SliderN2 = (event, newValue) => {
    setLTemp(newValue);
  };

  const handleN2 = (event) => {
    setLTemp(event.target.value === "" ? "" : Number(event.target.value));
  };

  useEffect(() => {
    setUrl(
      "/pH-metry?v1=" + vol_of_hcl + "&n1=0.01&n2=" + normality_NaOH + "&inc=2"
    );
  }, [vol_of_hcl, normality_NaOH]);

  return (
    <>
      {!sim && (
        <Grid container spacing={1}>
          <Grid item xs={3}>
            <Paper className={classes.paper}>
              <Paper elevation={0}>
                <Typography gutterBottom className="mt-5">
                  Volume of HCl taken in beaker (V<sub>1</sub>):
                </Typography>
                <Input
                  className={classes.input}
                  value={vol_of_hcl}
                  margin="dense"
                  readOnly
                  onChange={handleV1Change}
                  inputProps={{
                    step: 1,
                    min: 50,
                    max: 100,
                    type: "number",
                    "aria-labelledby": "input-slider",
                  }}
                />
                ml
                <br />
                <br />
                <Slider
                  aria-labelledby="discrete-slider"
                  valueLabelDisplay="auto"
                  onChange={SliderV1}
                  value={typeof vol_of_hcl === "number" ? vol_of_hcl : 25}
                  step={5}
                  marks
                  min={50}
                  max={100}
                />
                <br />
                <br />
                <Typography gutterBottom>
                  Normality of NaOH: (N<sub>2</sub>)
                </Typography>
                <Input
                  className={classes.input}
                  value={normality_NaOH}
                  margin="dense"
                  onChange={handleN2}
                  readOnly
                  inputProps={{
                    step: 0.01,
                    min: 0.01,
                    max: 0.1,
                    type: "number",
                    "aria-labelledby": "input-slider",
                  }}
                />
                N
                <br />
                <br />
                <Slider
                  aria-labelledby="discrete-slider"
                  valueLabelDisplay="auto"
                  onChange={SliderN2}
                  value={
                    typeof normality_NaOH === "number" ? normality_NaOH : 0.001
                  }
                  step={0.01}
                  marks
                  min={0.01}
                  max={0.1}
                />
                <br />
              </Paper>
            </Paper>
          </Grid>
          <Grid item xs={9}>
            <Paper className={classes.paper}>
              <Paper elevation={0}>
                <img src="/images/phMeter.png" width="95%" className="mt-5" />
                <Button
                  className="ml-auto mr-auto mt-5"
                  id="start"
                  variant="outlined"
                  color="primary"
                  onClick={(e) => {
                    setSim(true);
                  }}
                >
                  Start
                </Button>

                <br />
                <br />
              </Paper>
            </Paper>
          </Grid>
        </Grid>
      )}
      {sim && (
        <>
          <Iframe
            url={url}
            width="100%"
            height="750px"
            id="myId"
            className="myClassname"
            display="initial"
            position="relative"
          />
          <Paper elevation={0} className="text-center">
            <Button
              className="ml-auto mr-auto"
              id="start"
              style={{ textAlign: "center" }}
              variant="outlined"
              color="secondary"
              onClick={(e) => {
                setSim(false);
              }}
            >
              RESET
            </Button>
          </Paper>
        </>
      )}
    </>
  );
}
