// import "./sim2/canvas.js";
import "./sim2/style.css";
import "./sim2/dropsp.css";
import "./sim2/dropsm.css";

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    zIndex: "100",
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  root: {
    textAlign: "center",
    width: "100%",
    zIndex: "100",
  },
  paper: {
    padding: theme.spacing(3),
    textAlign: "center",
    zIndex: "100",
  },
  button: {
    textAlign: "center",
  },
  input: {
    width: "50px",
    zIndex: "100",
  },
  dropdown: {
    position: "absolute",
    top: 28,
    right: 0,
    left: 0,
    zIndex: 1,
    border: "1px solid",
    padding: theme.spacing(1),
    backgroundColor: theme.palette.background.paper,
  },
}));

function valuetext_vol(value) {
  return `${value}ml`;
}

function valuetext_mol(value) {
  return `${value}M`;
}

export default function SimulatorTab2(props) {
  const alk = {
    1: 420,
    2: 810,
  };
  const [open, setOpen] = React.useState(false);
  const classes = useStyles();
  const [indicator, setIndicator] = React.useState(0);
  const [volume_of_water, setVolume] = React.useState(10);
  const [normality_of_hcl, setNormality] = React.useState(0.01);
  const [speed, setSpeed] = React.useState(0.1);
  const [current_volume_of_v1, setPt] = React.useState(0); // phenolphthalein
  const [current_volume_of_v2, setMo] = React.useState(0); // methyl orange
  const [current_color, setColor] = React.useState("pink");
  const [result, setResult] = React.useState(0);
  const [user_result, setUresult] = React.useState(0);
  const [user_result1, setUresult1] = React.useState(0);
  const [correct, setCorrect] = React.useState(false);
  const [wrong, setWrong] = React.useState(false);

  const d1 = React.useRef(null);
  const d2 = React.useRef(null);
  const c1 = React.useRef(null);

  useEffect(() => {
    var canvas = document.querySelector("canvas");

    canvas.width = 950;
    canvas.height = 880;

    var burettesol = document.getElementById("burettesol");
    burettesol.height = "200";

    function loadImages(sources, callback) {
      var images = {};
      var loadedImages = 0;
      var numImages = 0;
      // get num of sources
      for (var src in sources) {
        numImages++;
      }
      for (var src in sources) {
        images[src] = new Image();
        images[src].onload = function () {
          if (++loadedImages >= numImages) {
            callback(images);
          }
        };
        images[src].src = sources[src];
      }
    }
    var context = canvas.getContext("2d");
    var methyladded = 0;
    var phenoadded = 0;
    var sources = {
      image1: "../images/burette2.png",
      image2: "../images/table.png",
      image3: "../images/rack.png",
      image4: "../images/flask.png",
      image5: "../images/flask_liquid.png",
      image6: "../images/flask_liquid_after_change.png",
      image7: "../images/burette_stand.png",
      image8: "../images/burette knob off.png",
      image9: "../images/burette_knob_on.png",
      image10: "../images/mstirrer.png",
      image11: "../images/white.png",
      image12: "../images/rim.png",
      image13: "../images/drop.png",
      image14: "../images/colorless_sol.png",
      image15: "../images/indicator_bottle.png",
      image16: "../images/dropper.png",
      image17: "../images/indicatorbsolp.png",
      image18: "../images/indicatorbsolm.png",
      image19: "../images/colorchaangeafterph.png",
      image20: "../images/colorchaangeafterme.png",
      image21: "../images/flask_liquid_me.png",
      image22: "../images/blank_flask_liquid.png",
      image23: "../images/phenolname.png",
      image24: "../images/methylname.png",
    };

    loadImages(sources, function (images) {
      context.drawImage(images.image2, 220, 640, 560, 350);
      // context.drawImage(images.image14, 408, 290, 15, 205);
      context.drawImage(images.image1, 339, 265, 245, 350);
      // context.drawImage(images.image3, 1150, 440, 410, 450);
      context.globalAlpha = 0.7;
      context.drawImage(images.image5, 316, 599, 217, 158);
      context.globalAlpha = 1;
      context.drawImage(images.image4, 360, 617, 130, 110);
      //context.drawImage(images.image6, 320, 715, 150, 150);
      context.drawImage(images.image7, 160, 236, 540, 640);
      context.drawImage(images.image8, 422, 500, 35, 45);
      //context.drawImage(images.image9, 410, 537, 35, 45)
      context.drawImage(images.image10, 365, 695, 120, 120);
      // context.drawImage(images.image12, 404, 240, 20, 19);
      // context.drawImage(images.image16, 500, 625, 150, 160);
      // context.drawImage(images.image16, 600, 625, 150, 160);
      context.globalAlpha = 0.4;
      context.drawImage(images.image17, 480, 616, 250, 250);
      context.drawImage(images.image18, 550, 616, 250, 250);
      context.globalAlpha = 1;
      context.drawImage(images.image15, 480, 616, 250, 250);
      context.drawImage(images.image15, 550, 616, 250, 250);

      context.globalAlpha = 0.8;
      context.drawImage(images.image23, 528, 790, 100, 40);
      context.drawImage(images.image24, 653, 790, 100, 40);
      context.globalAlpha = 1;
    });

    function reload() {
      methyladded = 0;
      loadImages(sources, function (images) {
        context.drawImage(images.image2, 220, 640, 560, 350);
        // context.drawImage(images.image14, 408, 290, 15, 205);
        context.drawImage(images.image1, 339, 265, 245, 350);
        // context.drawImage(images.image3, 1150, 440, 410, 450);
        context.globalAlpha = 0.7;
        context.drawImage(images.image5, 316, 599, 217, 158);
        context.globalAlpha = 1;
        context.drawImage(images.image4, 360, 617, 130, 110);
        //context.drawImage(images.image6, 320, 715, 150, 150);
        context.drawImage(images.image7, 160, 236, 540, 640);
        context.drawImage(images.image8, 422, 500, 35, 45);
        //context.drawImage(images.image9, 410, 537, 35, 45)
        context.drawImage(images.image10, 365, 695, 120, 120);
        // context.drawImage(images.image12, 404, 240, 20, 19);
        // context.drawImage(images.image16, 500, 625, 150, 160);
        // context.drawImage(images.image16, 600, 625, 150, 160);
        context.globalAlpha = 0.4;
        context.drawImage(images.image17, 480, 616, 250, 250);
        context.drawImage(images.image18, 550, 616, 250, 250);
        context.globalAlpha = 1;
        context.drawImage(images.image15, 480, 616, 250, 250);
        context.drawImage(images.image15, 550, 616, 250, 250);

        context.globalAlpha = 0.8;
        context.drawImage(images.image23, 528, 790, 100, 40);
        context.drawImage(images.image24, 653, 790, 100, 40);
        context.globalAlpha = 1;
      });
    }

    var flask = document.getElementById("mask");
    var h = 615;
    var h2 = 300;

    function start() {
      console.log("Start");
      document.getElementById("dropperm").style.display = "none";
      document.getElementById("dropperp").style.display = "none";

      loadImages(sources, function (images) {
        document.getElementById("drops").style.display = "block";
        context.drawImage(images.image4, 360, 617, 130, 110);
        context.drawImage(images.image9, 422, 500, 35, 45);
        // context.drawImage(images.image13, 408, h, 15, 15)
      });

      // setTimeout(stop, 6000);
    }

    function stop() {
      console.log("STOP");
      var t = document.getElementById("burettesol").style.top;
      var top = t.slice(0, -2);
      t = parseInt(t) + 15;
      top = t.toString();
      top = top + "px";
      var h = burettesol.height;
      h = h - 10;
      burettesol.style.top = top;
      burettesol.height = h;

      loadImages(sources, function (images) {
        document.getElementById("drops").style.display = "none";

        context.drawImage(images.image11, 432, 500, 35, 45);
        context.drawImage(images.image8, 422, 500, 35, 45);
        // context.drawImage(images.image11, 400, 635, 40, 100)
        context.drawImage(images.image4, 360, 617, 130, 110);
        // context.drawImage(images.image6, 324, 577, 187, 152);
      });
    }

    function color_change() {
      if (methyladded == 1) {
        loadImages(sources, function (images) {
          context.drawImage(images.image22, 316, 599, 217, 158);

          context.drawImage(images.image21, 316, 599, 217, 158);
        });
        //   if (phenoadded == 1) {
        //     methyladded = 0;
        //   } else {
        //     methyladded = 1;
        //     phenoadded = 0;
        //   }
      } else {
        loadImages(sources, function (images) {
          context.drawImage(images.image22, 316, 599, 217, 158);
          context.drawImage(images.image5, 316, 599, 217, 158);
        });
        //   console.log("Colour changed");
      }
    }

    var p = document.getElementById("dropper1");
    var m = document.getElementById("dropper2");

    function addp() {
      phenoadded = 1;

      document.getElementById("dropperm").style.display = "none";
      m.style.display = "block";

      var style = p.style.display;
      if (style === "block") {
        p.style.display = "none";
        document.getElementById("dropperp").style.display = "block";
      }

      loadImages(sources, function (images) {
        document.getElementById("dropsp").style.display = "block";
      });
      setTimeout(function stopdropper() {
        document.getElementById("dropsp").style.display = "none";
        document.getElementById("dropperp").style.display = "none";
        document.getElementById("dropper1").style.display = "block";
        loadImages(sources, function (images) {
          context.globalAlpha = 0.5;
          context.drawImage(images.image22, 316, 599, 217, 158);

          context.drawImage(images.image19, 316, 599, 216, 162);
          context.globalAlpha = 1;
        });
      }, 3000);
    }

    function addm() {
      methyladded = 1;
      document.getElementById("dropperp").style.display = "none";
      p.style.display = "block";

      var style = m.style.display;
      if (style === "block") {
        m.style.display = "none";
        document.getElementById("dropperm").style.display = "block";
      }

      loadImages(sources, function (images) {
        document.getElementById("dropsm").style.display = "block";
      });
      setTimeout(function stopdropper() {
        document.getElementById("dropsm").style.display = "none";
        document.getElementById("dropperm").style.display = "none";
        document.getElementById("dropper2").style.display = "block";
        loadImages(sources, function (images) {
          context.globalAlpha = 0.5;
          context.drawImage(images.image22, 316, 599, 217, 158);

          context.drawImage(images.image20, 316, 594, 217, 162);
          context.globalAlpha = 1;
        });
      }, 3000);
    }

    // const s = document.getElementById("start-btn");
    // s.onclick = () => {
    //   start();
    // };

    document.getElementById("cc").onclick = () => {
      color_change();
      console.log("Click triggered");
    };
    document.getElementById("reset").onclick = () => {
      reload();
      console.log("Reset");
    };
    document.getElementById("dropper1").onclick = () => {
      addp();
      console.log("Click triggered");
    };
    document.getElementById("dropper2").onclick = () => {
      addm();
      console.log("Click triggered");
    };

    document.getElementById("start").onclick = () => {
      start();
    };
    document.getElementById("stop").onclick = () => {
      stop();
    };

    // if (current_color === "color_less") {
    //   color_change();
    //   console.log("Colour changed");
    // }
  }, []);

  let loop = useRef(null);

  useEffect(() => {
    console.log(
      `Volume of acid consumed (V1)= ${current_volume_of_v1} | Color = ${current_color}`
    );

    let current_ph =
      (50000 * current_volume_of_v1 * normality_of_hcl) / volume_of_water;

    if (current_ph >= alk[indicator]) {
      setColor("color_less");
      c1.current.click();
      console.log("colour changed");
    }
  }, [current_volume_of_v1]);

  useEffect(() => {
    console.log(
      `Volume of acid consumed (V2)= ${current_volume_of_v2} | Color = ${current_color}`
    );

    let current_ph =
      (50000 * current_volume_of_v2 * normality_of_hcl) / volume_of_water;

    if (current_ph >= alk[indicator]) {
      setColor("red");
      c1.current.click();
    }
  }, [current_volume_of_v2]);

  const handleIChange = (event) => {
    const ind = event.target.value;
    if (current_color === "color_less" && ind == 2) {
      setIndicator(event.target.value);
      d2.current.click();
    } else if (current_color === "pink" && ind === 1) {
      setIndicator(event.target.value);
      d1.current.click();
    } else if (ind === 2 && indicator === 0) {
      alert("Choose Phenolphthalein first");
    }
  };

  const handleSliderChange = (event, newValue) => {
    setVolume(newValue);
  };

  const handleMolarityChange = (event, newValue) => {
    setNormality(newValue);
  };

  const handleSpeedChange = (event, newValue) => {
    setSpeed(newValue);
    console.log(speed);
  };

  const handleVChange = (event) => {
    setVolume(event.target.value === "" ? "" : Number(event.target.value));
  };
  const handleSChange = (event) => {
    setSpeed(event.target.value === "" ? "" : Number(event.target.value));
  };
  const handleMChange = (event) => {
    setNormality(event.target.value === "" ? "" : Number(event.target.value));
  };
  const handleRChange = (event) => {
    setUresult(event.target.value === "" ? "" : Number(event.target.value));
  };
  const handleR1Change = (event) => {
    setUresult1(event.target.value === "" ? "" : Number(event.target.value));
  };
  const handleBlur = () => {
    if (volume_of_water < 0) {
      setVolume(0);
    } else if (volume_of_water > 100) {
      setVolume(100);
    }
  };

  //observation
  const titration_step = async () => {
    if (indicator === 1) {
      await setPt((current_volume_of_v1) => current_volume_of_v1 + speed);
    } else {
      setColor("yellow");
      await setMo((current_volume_of_v2) => current_volume_of_v2 + speed);
    }
  };
  const start_titration = async () => {
    if (indicator !== 0) {
      loop.current = setInterval(titration_step, 1000);
      document.getElementById("start").style.display = "none";
      document.getElementById("stop").style.display = "block";
    } else {
      alert("Choose Indicator");
    }
  };

  const stop_titration = () => {
    clearInterval(loop.current);
    if (current_color === "color_less") {
      setIndicator(2);
      d2.current.click();
      setMo(current_volume_of_v1);
    }

    document.getElementById("stop").style.display = "none";
    document.getElementById("start").style.display = "block";
  };

  //result
  const calc_hardness = () => {
    // let r =
    //   (1e5 * volume_of_edta[type_of_water] * normality_of_hcl) /
    //   volume_of_water;
    // setResult(r);
  };

  const check_result = () => {
    if (parseInt(user_result) !== 420 && parseInt(user_result1) !== 810) {
      setCorrect(false);
      setWrong(true);
    } else {
      setCorrect(true);
      setWrong(false);
    }
  };

  const reset = () => {
    setIndicator(0);
    setVolume(10);
    setNormality(0.01);
    setSpeed(0.1);
    setPt(0); // phenolphthalein
    setMo(0); // methyl orange
    setColor("pink");
    setResult(0);
    setUresult(0);
    setUresult1(0);
  };

  const handleClick = () => {
    setOpen((prev) => !prev);
  };

  const handleClickAway = () => {
    setOpen(false);
  };
  useEffect(() => {
    var looper;
    var degrees = 0;

    function rotateAnimation(el, speed) {
      var elem = document.getElementById(el);
      if (navigator.userAgent.match("Chrome")) {
        elem.style.WebkitTransform = "rotate(" + degrees + "deg)";
      } else if (navigator.userAgent.match("Firefox")) {
        elem.style.MozTransform = "rotate(" + degrees + "deg)";
      } else if (navigator.userAgent.match("MSIE")) {
        elem.style.msTransform = "rotate(" + degrees + "deg)";
      } else if (navigator.userAgent.match("Opera")) {
        elem.style.OTransform = "rotate(" + degrees + "deg)";
      } else {
        elem.style.transform = "rotate(" + degrees + "deg)";
      }
      looper = setTimeout("rotateAnimation('" + el + "'," + speed + ")", speed);
      degrees++;
      if (degrees > 359) {
        degrees = 1;
      }
      console.log("needle rotating");
    }

    rotateAnimation("needle", 0.5);
  }, []);

  return (
    <>
      <Grid container spacing={1}>
        <Grid item xs={3}>
          <Paper className={classes.paper}>
            <Paper elevation={0}>
              <br />
              <br />
              <div className={classes.root}>
                <Typography id="discrete-slider" gutterBottom>
                  Volume of Alkaline Water:
                </Typography>
                <Input
                  className={classes.input}
                  value={volume_of_water}
                  margin="dense"
                  onChange={handleVChange}
                  // onBlur={handleBlur}
                  inputProps={{
                    step: 10,
                    min: 10,
                    max: 100,
                    type: "number",
                    "aria-labelledby": "input-slider",
                  }}
                />
                ml
                <br />
                <br />
                <Slider
                  defaultvolume={10}
                  getAriaValueText={valuetext_vol}
                  aria-labelledby="discrete-slider"
                  valueLabelDisplay="auto"
                  onChange={handleSliderChange}
                  value={
                    typeof volume_of_water === "number" ? volume_of_water : 0
                  }
                  step={10}
                  marks
                  min={10}
                  max={100}
                />
                <br />
                <br />
                <Typography id="discrete-slider" gutterBottom>
                  Normality of Titrant (Acid):
                </Typography>
                <Input
                  className={classes.input}
                  value={normality_of_hcl}
                  margin="dense"
                  onChange={handleMChange}
                  // onBlur={handleBlur}
                  inputProps={{
                    step: 0.01,
                    min: 0.01,
                    max: 0.1,
                    type: "number",
                    "aria-labelledby": "input-slider",
                  }}
                />
                N
                <br />
                <br />
                <Slider
                  defaultValue={10}
                  getAriaValueText={valuetext_mol}
                  aria-labelledby="discrete-slider"
                  valueLabelDisplay="auto"
                  value={
                    typeof normality_of_hcl === "number" ? normality_of_hcl : 1
                  }
                  step={0.01}
                  onChange={handleMolarityChange}
                  marks
                  min={0.01}
                  max={0.1}
                />
                <br />
                <br />
                <Typography id="discrete-slider" gutterBottom>
                  Speed of Titrant:
                </Typography>
                <Input
                  className={classes.input}
                  value={speed}
                  margin="dense"
                  onChange={handleSChange}
                  // onBlur={handleBlur}
                  inputProps={{
                    step: 0.1,
                    min: 0.1,
                    max: 1,
                    type: "number",
                    "aria-labelledby": "input-slider",
                  }}
                />
                <br />
                <br />
                <Slider
                  defaultvolume={1}
                  getAriaValueText={valuetext_vol}
                  aria-labelledby="discrete-slider"
                  valueLabelDisplay="auto"
                  onChange={handleSpeedChange}
                  value={typeof speed === "number" ? speed : 1}
                  step={0.1}
                  marks
                  min={0.1}
                  max={1}
                />
                <br />
                <br />
                <div>
                  <FormControl className={classes.formControl}>
                    <Typography gutterBottom>Indicator:</Typography>
                    <Select
                      labelId="demo-simple-select-label"
                      id="demo-simple-select"
                      defaultValue={0}
                      value={indicator}
                      onChange={handleIChange}
                    >
                      <MenuItem value={0}>None</MenuItem>
                      <MenuItem value={1}>Phenolphthalein</MenuItem>
                      <MenuItem value={2}>Methyl Orange</MenuItem>
                    </Select>
                  </FormControl>
                </div>
              </div>
            </Paper>
          </Paper>
        </Grid>
        <Grid item xs={6}>
          <div className="sim1">
            <div class="h-75 row">
              <div class="col-md-1"></div>
              <div class="col-md-10">
                <div class="row mt-4">
                  <div class="col-md-10 text-center">
                    <button
                      type="button"
                      class="btn btn-success"
                      style={{ opacity: "0" }}
                      id="cc"
                      ref={c1}
                    >
                      Start
                    </button>
                  </div>
                </div>
                <div class="row">
                  <div
                    class="drops"
                    id="drops"
                    style={{
                      display: "none",
                      position: "absolute",
                      left: "420px",
                      top: "660px",
                    }}
                  ></div>
                  <div
                    class="dropsp"
                    id="dropsp"
                    style={{
                      display: "none",
                      position: "absolute",
                      left: "420px",
                      top: "670px",
                    }}
                  ></div>
                  <div
                    class="dropsm"
                    id="dropsm"
                    style={{
                      display: "none",
                      position: "absolute",
                      left: "420px",
                      top: "670px",
                    }}
                  ></div>
                  <img
                    id="burettesol"
                    src="../images/colorless_sol.png"
                    style={{
                      position: "absolute",
                      left: "418px",
                      top: "348px",
                      width: "15px",
                      opacity: "0.6",
                    }}
                  />
                  <img
                    id="needle"
                    src="../images/stirrer1.svg"
                    width="10"
                    height="10"
                    style={{
                      position: "absolute",
                      left: "420px",
                      top: "765px",
                    }}
                  />
                  <img
                    id="dropper1"
                    // onclick="addp()"
                    ref={d1}
                    src="../images/dropper.png"
                    width="120"
                    height="125"
                    style={{
                      position: "absolute",
                      left: "545px",
                      top: "730px",
                      opacity: "0.9",
                      display: "block",
                    }}
                  />
                  <img
                    id="dropper2"
                    // onclick="addm()"
                    ref={d2}
                    src="../images/dropper.png"
                    width="120"
                    height="125"
                    style={{
                      position: "absolute",
                      left: "615px",
                      top: "730px",
                      opacity: "0.9",
                      display: "block",
                    }}
                  />
                  <img
                    id="dropperp"
                    src="../images/dropperp.png"
                    width="120"
                    height="140"
                    style={{
                      position: "absolute",
                      left: "405px",
                      top: "570px",
                      opacity: "0.9",
                      display: "none",
                    }}
                  />
                  <img
                    id="dropperm"
                    src="../images/dropperm.png"
                    width="120"
                    height="140"
                    style={{
                      position: "absolute",
                      left: "405px",
                      top: "570px",
                      opacity: "0.9",
                      display: "none",
                    }}
                  />
                  <canvas id="canvas"></canvas>
                </div>
              </div>
              <div class="col-md-1"></div>
            </div>
          </div>
          <Paper className={classes.paper}>
            <Paper elevation={0}>
              <Button
                className="ml-auto mr-auto"
                id="start"
                variant="outlined"
                color="primary"
                disabled={indicator === 0}
                onClick={(e) => {
                  start_titration();
                }}
              >
                Start
              </Button>
              <Button
                className="ml-auto mr-auto"
                id="stop"
                variant="outlined"
                color="danger"
                onClick={(e) => {
                  stop_titration();
                }}
              >
                Stop
              </Button>
              <br />
              <br />
              <Button
                variant="outlined"
                color="secondary"
                id="reset"
                onClick={(e) => {
                  reset();
                }}
              >
                Reset
              </Button>
            </Paper>
          </Paper>
        </Grid>
        <Grid item xs={3}>
          <Paper className={classes.paper}>
            <Paper elevation={0}>
              <b>OBSERVATION:</b>
              <br />
              <br />
              <b>
                <u>End Points:</u>
              </b>
              <br />
              <br />
              <b>Colour Change 1: </b> Pink turns Colourless
              <br />
              <b>Volume of titrant used (V1): </b>
              {current_volume_of_v1.toFixed(1)} ml
              <br />
              <br />
              <hr />
              <b>Colour Change 2: </b> Yellow turns Red
              <br />
              <br />
              <b>Volume of titrant used (V2): </b>
              {current_volume_of_v2.toFixed(1)} ml
              <br />
              <br />
              <b>Current Color:</b> {current_color}
              <hr />
              <br />
              <br />
              <b>
                <u>RESULT:</u>
              </b>
              <br />
              <br />
              <b>Enter your answer:</b> <br />
              Phenolphthalein Alkanality:
              <br />
              <Input
                className={classes.input}
                value={user_result}
                margin="dense"
                onChange={handleRChange}
                disabled={current_color !== "red"}
                // onBlur={handleBlur}
                inputProps={{
                  type: "number",
                }}
              />{" "}
              ppm
              <br />
              <br />
              Methyl Orange Alkanality:
              <br />
              <Input
                className={classes.input}
                value={user_result1}
                margin="dense"
                onChange={handleR1Change}
                // onBlur={handleBlur}
                disabled={current_color !== "red"}
                inputProps={{
                  type: "number",
                }}
              />{" "}
              ppm
              <br />
              <br />
              <Button
                variant="outlined"
                color="danger"
                disabled={!user_result1}
                onClick={(e) => {
                  check_result();
                }}
              >
                Submit
              </Button>
              {correct && (
                <Alert severity="success">Your answer is correct. Good!</Alert>
              )}
              {wrong && (
                <Alert severity="error">
                  Your answer is incorrect. Try Again!
                </Alert>
              )}
              <br />
              <hr />
              <br />
              {(correct || wrong) && (
                <div>
                  <Button
                    variant="outlined"
                    color="danger"
                    onClick={handleClick}
                  >
                    Show Result{" "}
                  </Button>
                  {open ? (
                    <div>
                      Phenolphthalein Alkanality:
                      <br />
                      420 ppm
                      <br />
                      <br />
                      Methyl Orange Alkanality:
                      <br />
                      810 ppm
                    </div>
                  ) : null}
                </div>
              )}
            </Paper>
          </Paper>
        </Grid>
      </Grid>
    </>
  );
}
