    aim: "Water Analysis - Hardness",
    objective:
      "To determine the total hardness of water by complexometric titration.  \r\n&nbsp;\r\n\r\n### **LEARNING OUTCOMES:**\r\n\r\nStudents will be able to calculate the hardness of water.\r\n\r\n&nbsp;\r\n\r\n### **APPARATUS:**\r\n\r\nBurette, conical flask, beaker, magnetic stirrer etc.\r\n\r\n&nbsp;\r\n\r\n### **CHEMICALS:** \r\n\r\nEthylene Diamine Tetra Acetic Acid (EDTA), Eriochrome Black T (EBT), Water sample, Buffer Solution (pH 8 -10)",
    apparatus: "Burette, pipette, conical flask, beaker etc.",
    chemicals:
      "Ethylene Diamine Tetra Acetic Acid (EDTA), Erio-chrome Black T (EBT), Water sample, Buffer Solution (of pH 9-10)",
    theory:
      "The hardness of water can be determined by complexometric titration. The complexometric titrations involve formation of a soluble complex between a metal ion and a complexing agent. The metal ion is called the central atom and the complexing agent is called the ligand. The ligands can be unidentate, bidentate or polydentate having one, two and more than two donor atoms respectively which can form stable complexes. The polydentate ligands form stable complexes with wide variety of metals as these ligands form ring structures called chelates. Ethylene Diamine Tetra Acetic acid (EDTA) is one of the notable ligand used as complexing reagent. EDTA forms stable complexes with the metal ions. EDTA has six potential sites (hexadentate) for bonding a metal ion, the two amino groups and four carboxy group. When EDTA is dissolved in water, it forms zwitterion (an ion with a positive and a negative electrical charge at different locations within a molecule). The various EDTA species are abbreviated as H~4~Y, H~3~Y^-^, H~2~Y^2-^, HY^3-^ and Y^4-^.\r\n\r\nThe relative amounts of these species is a function of pH.\r\n\r\nAt pH ≈ 5, EDTA exists as H2Y^2-^, for pH ≥ 8, EDTA exists as HY^3-^ and for pH >12, EDTA exists as Y^4-^. The free acid H4Y is not used for preparation of standard solution of EDTA because of its limited solubility. For analytical work, the disodium salt Na~2~H~2~Y is the most useful as it can be obtained in high state of purity as the dihydrate.\r\n\r\nEDTA solutions are used as Titrant. The metal ions M^2+^ (like Ca^2+^ and Mg^2+^) present in water are titrated with EDTA solution using Eriochrome Black-T as an indicator. Actually the di-sodium salt of EDTA is used in the titration and called as EDTA. It is tetra-basic acid. Both nitrogen and oxygen atoms in this are electron donors, hence it forms chelate complexes with many metal ions. EDTA solution combines with metal ion in a 1:1 ratio regardless of the charge on the cation, to form a stable cage like structure. The reactions of EDTA with cations may be written as:\r\n\r\nEquilibrium of these reactions is markedly affected by the pH of solution. The complexes of divalent metals are stable in ammoniacal solution.\r\n\r\nIndicators used in this EDTA titration are the organic colouring substances that undergo change of colour when they are form metal complexes. The indicator forms a less stable complex with the metal atom than the EDTA-Metal complex. The indicator releases metal ion at equivalence point and this results into change in colour. The most widely used indicator is Eriochrome Black T (EBT). The ionized form of EBT is red in colour. The phenolic group present on EBT may also get ionized at higher pH values. Hence, it would exhibits different colours, depending upon the pH of the solution i.e. it is a pH sensitive indicator. It shows red colour at pH < 6, blue colour for pH > 7 and orange colour for pH > 10.\r\n\r\nThus, pH of the titrating solution has to be maintained by adding a suitable buffer solution.\r\n\r\nEDTA titrations are performed in buffers of pH 8-10, the range in which indicator, EBT shows blue colour.\r\n\r\nWhen the metallic salts are added to indicator, the colour of the indicator changes from blue to red as indicator complexes with metal ion. At the end point in EDTA titration, the indicator will be set free because of the formation of a stable metal EDTA complex. The end point will be colour change from red to blue.\r\n\r\nEstimation of hardness by EDTA method is based on the following principle.\r\n\r\n1. First, the indicator EBT, which is a blue coloured dye, forms an unstable complex with calcium and magnesium ions in hard water at a pH of 9 to 10. The complex is wine red in colour.\r\n\r\nMg^2+^ / Ca^2+^ + EBT &rarr; [Mg^2+^ / Ca^2+^ - EBT] Unstable Complex (wine red)\r\n\r\n2. As this solution is titrated against EDTA, the free Ca and Mg ions in water forms stable metal-ions EDTA complex.\r\n\r\nMg^2+^ / Ca^2+^ + EDTA &rarr; [Mg^2+^ / Ca^2+^ - EDTA] Stable Complex (colourless)\r\n\r\nOnce the free metal ions are complexed, the EDTA replaces Ca^2+^ and Mg^2+^ ions from the unstable indicator complex also, to form a stable complex, with the result, indicator is set free. Since the free indicator is blue in colour at the above mentioned PH, the end point is the appearance of blue colour.\r\n\r\n&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; [Mg^2+^ / Ca^2+^ - EBT] + EDTA &rarr; [Mg^2+^ / Ca^2+^ - EDTA] + EBT (blue)\r\n\r\nThus, the amount of EDTA used corresponds to the hardness of water.\r\n\r\nThe temporary hardness is removed by boiling and after the removal of precipitate by filtration; the permanent hardness in the filtrate is determined by titration with EDTA as above.\r\n\r\nTemporary hardness will be given by the difference of total hardness and permanent hardness. Hardness in water is generally expressed in CaCO~3~ equivalent. \r\n\r\nUnits of hardness: milligrams per liter (mg/l) / parts per million (ppm)/ &deg; Clarke/ &deg; French\r\n\r\n1mg/l = 1 ppm",
    procedure:
      "1. Select the type of water sample in the conical flask.\r\n2. Select the volume of water (for example 10ml) \r\n3. Check the indicator (Eriochrome Black T) and buffer solution of pH 10 used with water sample to be analyzed.\r\n4. Select the molarity of titrant (EDTA)\r\n5. Set the speed of titrant to be added in the conical flask\r\n6. Titrate the water sample against EDTA solution by pressing the start button.\r\n7. Stop the button when the colour of the solution (reaction mixture) changes from wine red to blue.\r\n\r\n&nbsp;\r\n\r\n### **OBSERVATIONS:**\r\n\r\nIn Conical flask: ______ ml of water sample (of unknown hardness)\r\n\r\nIn Burette: _________M EDTA solution\r\n\r\nIndicator Used: Eriochrome Black T (EBT)\r\n\r\nEnd point: Wine red turns Blue\r\n\r\nVolume of titrant consumed till end point: _______ml of EDTA\r\n\r\n&nbsp;\r\n\r\n### **CALCULATIONS:**\r\n\r\nEDTA forms 1 : 1 complex with Ca and Mg ions\r\n\r\n1 mole of EDTA = 1 mole of CaCO~3~\r\n\r\n1000 ml of 1 M EDTA = 100 g of CaCO~3~\r\n\r\n1 ml 1 M EDTA = 100 mg of CaCO~3~\r\n\r\n'Y' ml 'Z' M EDTA = 100 x Y x Z mg of CaCO~3~\r\n\r\nHardness of 'V' ml water sample = (100 x Y x Z) mg of CaCO~3~\r\n\r\n![enter image description here](https://raw.githubusercontent.com/PICT-Chemistry-V-Lab/pict-chemistry-v-lab.github.io/master/images/1png.png)&nbsp;\n" +
        "\n" +
        "&nbsp;\n" +
        "\n" +
        "### **RESULT:**\n" +
        "\n" +
        "Hardness of water sample = ... ppm",
