    "assignment": "1.  What is hard water? Mention the causes for hardness of water.\r\n    \r\n2.  Why hardness is defined as soap consuming capacity of water?\r\n    \r\n3.  What is temporary hard water? Which mineral causes temporary hardness?\r\n    \r\n4.  What is permanent hard water? Which mineral causes permanent hardness?\r\n    \r\n5.  How to eliminate temporary hardness of water? Explain.\r\n    \r\n6.  Suggest chemical methods to eliminate permanent hardness of water.\r\n    \r\n7.  In which units hardness of water can be expressed?\r\n    \r\n8.  What is complexometric titration?\r\n    \r\n9.  Give the procedure, reaction & formulae for determination of hardness of water using EDTA method.\r\n    \r\n10.  Why is it necessary to add buffer solution in the water sample?\r\n    \r\n11.  Why EDTA method is preferred to determine the hardness of water?\r\n    \r\n12.  50 ml of hard water sample consumed 15 ml of 0.01 M EDTA before boiling and 5 ml of same EDTA after boiling. Calculate the degree of total hardness, permanent hardness and temporary hardness. (total hardness = 300 ppm, permanent hardness = 100 ppm and temporary hardness = 200 ppm)",
