    "assignment": "1.  Define alkalinity.\r\n    \r\n2.  Write a note on Alkalinity.\r\n    \r\n3.  Why alkalinity of water cannot be due to simultaneous presence of , and ions.\r\n    \r\n4.  If the water sample is not alkaline to phenolphthalein (P=0), then what type of alkalinity is present in water.\r\n    \r\n5.  What is phenolphthalein alkalinity? What do you mean by total alkalinity?\r\n    \r\n6.  What is the significance to calculate alkalinity?\r\n    \r\n7.  100 ml of water sample require 20 ml of N/50 H~2~SO~4~ for neutralization to phenolphthalein end point. After this, methyl orange indicator was added to this and further acid required was again 20 ml. Calculate the alkalinity of water as CaCO~3~ in ppm. (P alkalinity=200 mg/l, M alkalinity=400 mg/l, CO~3~^--^ alkalinity= 400 mg/l)\r\n    \r\n8.  100 ml of water sample needed 16.9 ml of N/50 HCl with methyl orange and has no phenolphthalein alkalinity. Calculate the alkalinity present in the water sample. (169 mg/l)\r\n    \r\n9.  100 ml of raw water sample on titration with N/50 H~2~SO~4~ required 12.4 ml of the acid to phenolphthalein end point and 15.2 ml of the same acid to methyl orange end point. Determine the type and extent of alkalinity present in the water sample. (OH^-^ = 96 mg/l, CO~3~^--^ = 56 mg/l and total alkalinity = 152 mg/l)\r\n    \r\n10.  100 ml of water sample requires 4.7 ml of 0.02 N HCl up to phenolphthalein endpoint and total 11.9 up to methyl orange endpoint. Calculate the type and amount of alkalinity present. (CO~3~^--^ alkalinity = 94 mg/l and HCO~3~^-^ = 25 mg/l)",
    "reference": "1.  Engineering Chemistry, Wiley India Pvt Ltd\r\n    \r\n2.  Engineering Chemistry, Jain & Jain, Dhanpat Rai Publishing House\r\n    \r\n3.  Engineering Chemistry, O.G. Palanna, Tata McGraw Hill Education Pvt. Ltd.\r\n    \r\n4.  A Text Book of Engineeringg Chemistry, Dr. S. S. Dara, Dr. S. S. Umare,\r\n    \r\n5.  Inorganic Chemistry, Shriver & Atkins, Oxford University Press.\r\n    \r\n6.  Vogel’s Text book of Quantitative Chemical Analysis, 6e, J Mendham, R C Denney, J D Barnes, MKJ Thomas, Pearson Education Ltd.\r\n    \r\n7.  Applied Chemistry Theory and practice, 2e, O P Virmani and A K Narula, New age International (P) Ltd."
