    objective:
      "To determine &lambda;~max~ of solution of Potassium permanganate (KMnO~4~) and to verify Beer”s Law and find concentration of unknown solution of given sample. \r\n\r\n\r\n&nbsp;\r\n\r\n### **LEARNING OUTCOMES:**\r\n\r\nStudent will be able to explain the fundamental principle behind spectrophotometric analysis, characteristic wavelength of solutions and the Beer’s law to determine molar absorptivity.\r\n\r\n&nbsp;\r\n\r\n### **APPARATUS:**\r\n\r\nUV-visible spectrophotometer, beakers, quartz cuvettes, volumetric flasks, beaker, measuring cylinder.\r\n\r\n&nbsp;\r\n\r\n### **CHEMICALS:** \r\n\r\nDistilled water, standard solution of KMnO~4~, tissue paper",
