        "28.  Determine the concentration (unknown) of KMnO~4~ solution from the plot of absorbance (at &lambda;~max~) verses concentration of KMnO~4~ solution.\r\n\r\n  \r\n\r\n&nbsp;\r\n\r\n  \r\n\r\n**OBSERVATIONS**  **& CALCULATIONS:**\r\n\r\n1. Determination of &lambda;~max~: From the graph of absorbance verses wavelength, determine the wavelength of maximum absorption (&lambda;~max~) for the sample.  \r\n\r\n&lambda;~max~ of the given KMnO~4~ solution = ____ nm \r\n \r\n\r\n2. Verification of Beer’s law: A curve is plotted between absorbance and concentration.  \r\n\r\nObserved a linear increase in the absorbance with the increase in concentration of KMnO~4~ solution; hence, Beers law is verified.\r\n\r\n3. Determination of concentration (unknown) of KMnO~4~ solution: From the graph of absorbance verses concentration of KMnO~4~ solution, determine the unknown concentration.\r\n\r\nFrom the graph, the unknown concentration of KMnO~4~ solution = _______ M\r\n\r\n  \r\n\r\n  \r\n\r\n  \r\n\r\n  \r\n\r\n  \r\n\r\n### **RESULT :**\r\n\r\n  \r\n\r\n1. &lambda;~max~ of given (KMnO~4~) solution = ______ nm\r\n\r\n  \r\n\r\n2. Concentration (unknown) of the given (KMnO~4~) solution = ____ M",
