    aim: "Phenol Formaldehyde Resin",
    objective:
      "Preparation of phenol formaldehyde resin (Bakelite)\r\n\r\n&nbsp;\r\n\r\n### **LEARNING OUTCOMES:**\r\n\r\nStudents shall be able to prepare polymeric resin.\r\n\r\n&nbsp;\r\n\r\n### **APPARATUS:**\r\n\r\nGlass rod, beakers, funnel, filter papers, etc.\r\n\r\n&nbsp;\r\n\r\n### **CHEMICALS:** \r\n\r\nGlacial acetic acid, 40 % formaldehyde solution (formaline), phenol, conc. HCl / H~2~SO~4~",
    apparatus: "Glass rod, beakers, funnel, filter papers, etc.",
    chemicals:
      "Glacial acetic acid, 40 % formaldehyde solution, phenol, conc. HCl",
    theory:
      "Phenol formaldehyde resins are the most important and extensively used synthetic plastics. In this resins one of the monomers is always a phenol molecule. One of the most important members of this class is phenol formaldehyde resin. The phenol formaldehyde reaction is catalyzed by alkalies and acids. The nature of the product depends on several factors the major factors are the nature of the catalyst and the proportion of the reactants. The formation of phenol formaldehyde resin comprises of the following steps.\r\n\r\n**(i) Methylolation:** When phenol and formaldehyde react together, the first step is the entry of methylol (CH2OH) groups in ortho and para positions to the hydroxyl group. The reaction takes place in the presence of acid or alkali. Depending upon phenol formaldehyde ratio, various phenol alcohols may be formed. \r\n ![Image 7.1](https://pict-chemistry-v-lab.github.io/images/7.1.png) \r\n\r\n\r\n\r\n\r\n**ii) Novolac formation:** Depending upon the ratio of phenol formaldehyde (P/F) different resins, namely novolac and resol resins are obtained. In the presence of acid catalyst, when the P/F ratio is greater than unity, the methylol derivatives condense with phenol to form a linear polymer with little methylol groups. The product is thermoplastic in nature and is known as novolac. \r\n ![Image 7.2](https://pict-chemistry-v-lab.github.io/images/7.2.png) \r\n\r\n**iii) Resole formation:** In the presence of alkaline catalysts with P/F ratio less than 1, the methylol phenols condense to form linear structure called resols. The resols are soluble in the reaction mixture and have excess of methylol groups capable of further reaction during continued heating. Resols, therefore have limited shelf-life. However, the reaction may be stopped at the desired step by cooling. \r\n\r\n**iv) Resite formation:** On further heating, resol is transformed into a three dimensional polymer, called resite, due to the presence of active groups and mobile hydrogens. Resite is an infusible and insoluble material. \r\n ![Image 7.3](https://pict-chemistry-v-lab.github.io/images/7.3.png) \r\n\r\n&nbsp;\r\n\r\n Acetic Acid is used as a solvent in preparation of Bakelite. Acetic acid is a hydrophilic protic solvent, much like water or ethanol. It dissolves both polar and nonpolar compounds and is miscible in both polar (water) and nonpolar (hexane, chloroform) solvents.  \r\n&nbsp;\r\n&nbsp;\r\n### **PROPERTIES :**\r\ni) Phenol formaldehyde resins have excellent heat resistance.\r\n\r\nii) These have high dimensional stability.\r\n\r\niii) To improve the properties further, fillers such as wood, flour, mica, asbestos etc. are added to them.\r\n\r\niv) Phenolic resins have good dielectric properties and bonding strength. They are, however attacked by alkalis, because of the presence of free hydroxyl groups.\r\n\r\nv) They are hard, rigid and scratch-resistant.\r\n\r\n### **USES :**\r\n\r\nPhenolic resins have wide applications.\r\n\r\ni) The main use of phenolic resin is moulding applications. They are widely used in making telephone parts, cabinets for radio, television and automobile parts.\r\n\r\nii) Due to their adhesive properties and bonding strength, they are used for producing brake linings, abrasive wheels and sand paper.\r\n\r\niii) They are used for making electric insulator parts like switches, plugs, switch boards, heater-handles etc.\r\n\r\niv) Phenolic resins are used for impregnating paper, wood and other fillers.\r\n\r\nv) These are also used in varnishes, paints and protective coatings.\r\n\r\nvi) These are used in the production of ion exchange resins for water softening.",
    procedure:
      "1) Place 5 ml of glacial acetic acid and 2.5 ml of 40% formaldehyde solution in a beaker.\r\n\r\n2) Add 2 g of phenol to it.\r\n\r\n3) Add a few ml of conc. HCl into the mixture carefully and heat it slightly.\r\n\r\n4) A large mass of plastic pink in color is formed.\r\n\r\n\r\n&nbsp;\r\n  \r\n### **RESULT:**\r\n\r\nPink color phenol formaldehyde resin is obtained.",
    observations: ".",
    calculations: ".",
    result_table: ".",
    "assignment": "1. Define the following terms: Monomer, Polymer, Functionality, Copolymer, Degree of Polymerisation, Glass transition temperature and Tacticity.\r\n\r\n2. Differentiate between: \r\n\r\n a) Thermoplastics & Thermosetting plastics.\r\n\r\n b) Chain or addition polymerization & step or condensation polymerization\r\n3. Give applications of phenol formaldehyde resin\r\n4. Give properties of phenol formaldehyde resin",
    "reference": "1.  Engineering Chemistry, Wiley India Pvt Ltd\r\n    \r\n2.  Engineering Chemistry, Jain & Jain, Dhanpat Rai Publishing House\r\n    \r\n3.  Engineering Chemistry, O.G. Palanna, Tata McGraw Hill Education Pvt. Ltd.\r\n    \r\n4.  A Text Book of Engineeringg Chemistry, Dr. S. S. Dara, Dr. S. S. Umare,\r\n    \r\n5.  Inorganic Chemistry, Shriver & Atkins, Oxford University Press.\r\n    \r\n6.  Vogel’s Text book of Quantitative Chemical Analysis, 6e, J Mendham, R C Denney, J D Barnes, MKJ Thomas, Pearson Education Ltd.\r\n    \r\n7.  Applied Chemistry Theory and practice, 2e, O P Virmani and A K Narula, New age International (P) Ltd."
