import {
  Button,
  CssBaseline,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormControl,
  FormControlLabel,
  makeStyles,
  Radio,
  RadioGroup,
} from "@material-ui/core";

import React, { useEffect, useState } from "react";
import { questions } from "./PreTest";
import { questions1 } from "./PostTest";

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: 15,
    paddintTop: 0,
  },
  container: {
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3),
  },
  OnlineResults: {
    marginTop: theme.spacing(3),
  },
  dialogue: {
    padding: 15,
  },
  dialogueTitle: {
    paddingTop: 15,
    paddingBottom: 15,
  },
}));

const TestPage = (props) => {
  const [question, setQuestion] = useState([]);
  const [marks, setMarks] = React.useState(0);

  const [ans, setAns] = useState([]);
  const [totMarks, setTotMarks] = useState(null);
  const [isSubmit, setIsSubmit] = useState(false);
  const [open, setOpen] = React.useState(false);
  const [isFinished, setIsFinished] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = (value) => {
    setOpen(false);
  };

  const getQuestions = async (test) => {
    let shuffled = [];
    if (props.test === "preTest")
      // Shuffle array
      shuffled = questions[props.id].sort(() => 0.5 - Math.random());
    else shuffled = questions1[props.id].sort(() => 0.5 - Math.random());

    // Get sub-array of first n elements after shuffled
    let selected = shuffled.slice(0, 4);
    setQuestion(selected);
    console.log(selected);
  };

  useEffect(() => {
    reset();
    getQuestions();
    console.log("getting questions");
  }, []);

  const handleChange = (i, event) => {
    ans[i - 1] = event.target.value;
    if (ans.length === 4) setIsFinished(true);
    else setIsFinished(false);
  };

  useEffect(() => {
    setAns([]);
  }, []);

  const handleSubmit = () => {
    console.log(ans);
    let m = 0;
    let cans = [];
    for (let index = 0; index < question.length; index++) {
      console.log(ans[index], question[index].correct_ans);
      if (ans[index] !== question[index].correct_ans) {
        question[index].quesColor = "rgb(255,0,0,0.3)";
      } else if (ans[index] == question[index].correct_ans) {
        m += 1;
        question[index].quesColor = "rgb(0,255,0,0.3)";
        setMarks(m);
        // console.log("marks", marks);
      }
      cans.push(question[index].correct_ans);
    }
    console.log(cans);
    // getQuestions(props.match.params.id);
    // setAns(cans);
    setTotMarks(m);
    setIsSubmit(true);
  };

  useEffect(() => {
    console.log(totMarks);
    if (totMarks != null) handleClickOpen();
  }, [totMarks]);

  const reset = () => {
    setQuestion([]);
    setMarks(0);
    setAns([]);
    setTotMarks(null);
    setIsSubmit(false);
    setIsFinished(false);
    questions[props.id].map((ques) => {
      ques.quesColor = "transparent";
    });
  };

  return (
    <>
      <CssBaseline />

      <div className="container">
        <br />
        <div className="text-left">
          <hr />
          <ol>
            {question.map((ques, index) => (
              <div style={{ backgroundColor: ques.quesColor, padding: "10px" }}>
                <h5>
                  <li>{ques.question}</li>{" "}
                  <FormControl component="fieldset">
                    <RadioGroup
                      aria-label="answer"
                      name="answers"
                      value={ans[index + 1]}
                      onChange={(e) => handleChange(index + 1, e)}
                    >
                      <FormControlLabel
                        disabled={isSubmit}
                        value={"1"}
                        control={<Radio />}
                        label={ques.option1}
                      />
                      <FormControlLabel
                        disabled={isSubmit}
                        value={"2"}
                        control={<Radio />}
                        label={ques.option2}
                      />
                      <FormControlLabel
                        disabled={isSubmit}
                        value={"3"}
                        control={<Radio />}
                        label={ques.option3}
                      />
                      <FormControlLabel
                        disabled={isSubmit}
                        value={"4"}
                        control={<Radio />}
                        label={ques.option4}
                      />
                    </RadioGroup>
                  </FormControl>
                  <hr />
                </h5>
              </div>
            ))}
          </ol>
        </div>
        <div className="my-5 text-center">
          <Button
            variant="contained"
            color="primary"
            onClick={handleSubmit}
            disabled={!isFinished}
          >
            Submit Test
          </Button>
        </div>
      </div>
      <Dialog
        onClose={handleClose}
        aria-labelledby="simple-dialog-title"
        open={open}
        fullWidth={"sm"}
      >
        <DialogTitle id="simple-dialog-title">Test Score</DialogTitle>
        <DialogContent>You scored : {totMarks}/4</DialogContent>
        <br />
        <br />
        <DialogActions>
          <Button
            onClick={handleClose}
            color="primary"
            variant="contained"
            autoFocus
          >
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default TestPage;
