import React, { useRef, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import Typography from "@material-ui/core/Typography";
import Slider from "@material-ui/core/Slider";
import Button from "@material-ui/core/Button";
import Input from "@material-ui/core/Input";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Alert from "@material-ui/lab/Alert";
import "./sim1/sim1.css";
import { Popover } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  root: {
    textAlign: "center",
    width: "100%",
  },
  paper: {
    position: "relative",
    padding: theme.spacing(3),
    textAlign: "center",
    height: "100%",
  },
  paper1: {
    padding: theme.spacing(3),
    textAlign: "center",
  },
  button: {
    textAlign: "center",
  },
  input1: {
    width: "45px",
    marginLeft: "5px",
  },
  tleft: {
    textAlign: "left",
  },
  tright: {
    textAlign: "right",
    marginRight: "30px",
  },
  popover: {
    pointerEvents: "none",
  },
}));

function valuetext_vol(value) {
  return `${value}ml`;
}

function valuetext_mol(value) {
  return `${value}M`;
}

export default function SimulatorTab() {
  // const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(false);

  const handlePopoverOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handlePopoverClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);

  const volume_of_edta = {
    1: 1.2,
    3: 2.2,
    2: 8,
  };

  let loop = useRef(null);

  const classes = useStyles();
  const [type_of_water, setWater] = React.useState(1);
  const [volume_of_water, setVolume] = React.useState(10);
  const [molarity_of_edta, setMolarity] = React.useState(0.01);
  const [speed, setSpeed] = React.useState(0.1);
  const [current_volume_of_edta, setObs] = React.useState(0);
  const [current_color, setColor] = React.useState("wine_red");
  const [result, setResult] = React.useState(null);
  const [user_result, setUresult] = React.useState(0);
  const [correct, setCorrect] = React.useState(false);
  const [wrong, setWrong] = React.useState(false);

  useEffect(() => {
    console.log(
      `Volume of EDTA Consumed = ${current_volume_of_edta} | Color = ${current_color}`
    );

    if (current_volume_of_edta >= volume_of_edta[type_of_water]) {
      console.log("yo");
      setColor("blue");
      document.getElementById("wave").style.background = "blue";
      document.getElementById("dum").style.borderBottomColor = "blue";
    }
  }, [current_volume_of_edta]);

  const handleChange = (event) => {
    setWater(event.target.value);
  };

  const handleSliderChange = (event, newValue) => {
    setVolume(newValue);
  };

  const handleMolarityChange = (event, newValue) => {
    setMolarity(newValue);
  };

  const handleSpeedChange = (event, newValue) => {
    setSpeed(newValue);
    console.log(speed);
  };

  const handleVChange = (event) => {
    setVolume(event.target.value === "" ? "" : Number(event.target.value));
  };
  const handleSChange = (event) => {
    setSpeed(event.target.value === "" ? "" : Number(event.target.value));
  };
  const handleMChange = (event) => {
    setMolarity(event.target.value === "" ? "" : Number(event.target.value));
  };
  const handleRChange = (event) => {
    setUresult(event.target.value === "" ? "" : Number(event.target.value));
  };

  //observation
  const titration_step = async () => {
    await setObs((current_volume_of_edta) => current_volume_of_edta + speed);
  };
  const start_titration = async () => {
    loop.current = setInterval(titration_step, 1000);
    document.getElementById("start").style.display = "none";
    document.getElementById("stop").style.display = "block";
    document.getElementById("drop").style.animation =
      "drip " + 0.2 / speed + "s cubic-bezier(1, 0, 0.91, 0.19) infinite";
    document.getElementById("nosel-hook").style.transform = "rotateX(0deg)";
  };
  useEffect(() => {
    document.getElementById("burette").style.height =
      40 - current_volume_of_edta + "%";
  }, [current_volume_of_edta]);

  const stop_titration = () => {
    clearInterval(loop.current);
    document.getElementById("stop").style.display = "none";
    document.getElementById("start").style.display = "block";
    document.getElementById("drop").style.animation = "none";
    document.getElementById("nosel-hook").style.transform = "rotateX(70deg)";
  };

  //result
  const calc_hardness = () => {
    let r =
      (1e5 * volume_of_edta[type_of_water] * molarity_of_edta) /
      volume_of_water;
    setResult(r);
  };

  const check_result = () => {
    let r =
      (1e5 * volume_of_edta[type_of_water] * molarity_of_edta) /
      volume_of_water;
    if (parseInt(user_result) !== parseInt(r)) {
      setCorrect(false);
      setWrong(true);
    } else {
      setCorrect(true);
      setWrong(false);
    }
  };

  const reset = () => {
    stop_titration();
    setWater(1);
    setVolume(10);
    setMolarity(0.01);
    setSpeed(0.1);
    setResult(null);
    setObs(0);
    setUresult(0);
    setWrong(false);
    setCorrect(false);
    setColor("wine_red");
    document.getElementById("wave").style.background = "#d01a31";
    document.getElementById("dum").style.borderBottomColor = "#d01a31";
  };
  return (
    <>
      <Grid container spacing={1}>
        <Grid item xs={3}>
          <Paper className={classes.paper}>
            <Paper elevation={0}>
              <div>
                <FormControl className={classes.formControl}>
                  <Typography gutterBottom>Type of Water:</Typography>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    defaultValue={1}
                    value={type_of_water}
                    onChange={handleChange}
                  >
                    <MenuItem value={1}>Tap Water</MenuItem>
                    <MenuItem value={2}>Sea Water</MenuItem>
                    <MenuItem value={3}>Well Water</MenuItem>
                  </Select>
                </FormControl>
              </div>
              <br />
              <br />
              <div className={classes.root}>
                <Typography id="discrete-slider" gutterBottom>
                  Volume of Water Sample:
                </Typography>
                <Input
                  className={classes.input}
                  value={volume_of_water}
                  margin="dense"
                  onChange={handleVChange}
                  // onBlur={handleBlur}
                  inputProps={{
                    step: 10,
                    min: 10,
                    max: 100,
                    type: "number",
                    "aria-labelledby": "input-slider",
                  }}
                />
                ml
                <br />
                <br />
                <Slider
                  defaultvolume={10}
                  getAriaValueText={valuetext_vol}
                  aria-labelledby="discrete-slider"
                  valueLabelDisplay="auto"
                  onChange={handleSliderChange}
                  value={
                    typeof volume_of_water === "number" ? volume_of_water : 0
                  }
                  step={10}
                  marks
                  min={10}
                  max={100}
                />
                <br />
                <br />
                <Typography id="discrete-slider" gutterBottom>
                  Molarity of Titrant (EDTA):
                </Typography>
                <Input
                  className={classes.input}
                  value={molarity_of_edta}
                  margin="dense"
                  onChange={handleMChange}
                  // onBlur={handleBlur}
                  inputProps={{
                    step: 0.01,
                    min: 0.01,
                    max: 0.1,
                    type: "number",
                    "aria-labelledby": "input-slider",
                  }}
                />
                M
                <br />
                <br />
                <Slider
                  defaultValue={10}
                  getAriaValueText={valuetext_mol}
                  aria-labelledby="discrete-slider"
                  valueLabelDisplay="auto"
                  value={
                    typeof molarity_of_edta === "number" ? molarity_of_edta : 1
                  }
                  step={0.01}
                  onChange={handleMolarityChange}
                  marks
                  min={0.01}
                  max={0.1}
                />
                <br />
                <br />
                <Typography id="discrete-slider" gutterBottom>
                  Speed of Titrant:
                </Typography>
                <Input
                  className={classes.input}
                  value={speed}
                  margin="dense"
                  onChange={handleSChange}
                  // onBlur={handleBlur}
                  inputProps={{
                    step: 0.1,
                    min: 0.1,
                    max: 1,
                    type: "number",
                    "aria-labelledby": "input-slider",
                  }}
                />
                <br />
                <br />
                <Slider
                  defaultvolume={1}
                  getAriaValueText={valuetext_vol}
                  aria-labelledby="discrete-slider"
                  valueLabelDisplay="auto"
                  onChange={handleSpeedChange}
                  value={typeof speed === "number" ? speed : 1}
                  step={0.1}
                  marks
                  min={0.1}
                  max={1}
                />
                <br />
                <br />
                <div className={classes.tleft}>
                  <b> Buffer Solution:</b> Alkaline Buffer{" "}
                  <div className={classes.tright}> (pH: 8-10){"   "}</div>
                  <br />
                  <b>Indicator:</b> Eriochrome Black T
                  <br />
                </div>
              </div>
            </Paper>
          </Paper>
        </Grid>
        <Grid item xs={6}>
          <div className="sim">
            <div class="ab">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                stroke="#bbb"
                width="110px"
                height="110px"
                viewbox="0 0 100 100"
                stroke-width="4px"
                fill="transparent"
                stroke-linejoin="round"
                top="40px"
                right="40px"
              >
                {/* <!--   <defs>
                        <mask id="mask">
                            <g stroke-width="0" fill="#000" >
                                <path fill="#fff" d="m0,50 h25 C50,35 50,65 75,50 h25 v50 H0" />

                            </g>
                        </mask>
                    </defs> --> */}

                <path
                  d="m35,2 h30 q-5,0 -5,30 L90,82 Q98,98 80,98 L20,98
  Q0,98 10,82 L40,32 Q40,2 35,2 h1"
                />

                {/* <!--   <path fill="#f50" stroke-width="0" mask="url(#mask)"
                        d="m50,27 L80,77 C90,93 90,93 70,93 L40,93
  C10,93 10,93 20,77"/>
 --> */}
              </svg>
              <div class="wave" id="wave">
                <div class="box"></div>
              </div>
              {/* <!-- Just for adjustment with fluid --> */}
              <div class="dum" id="dum"></div>
            </div>

            {/* <!-- water droplet --> */}
            <div class="drop" id="drop"></div>

            <div class="stand">
              <div class="base">
                <div class="base-block"></div>
                <div class="ring-stand"></div>
                <div class="hook"></div>
                <div class="rotating-hook"></div>
                <div class="clamp"></div>
              </div>

              <div class="burette" id="burette"></div>
              <div class="burette-bottom"></div>
              <div class="glass">
                {/* <!-- Leveling on burette --> */}
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
                <div class="level"></div>
              </div>
              {/* <!-- <div class="burette-nosel"></div> --> */}
              <div class="nosel-rod">
                {/* <!-- <div class="circle-cap"></div> --> */}
                <div class="nosel-hook" id="nosel-hook"></div>
              </div>
            </div>

            <div class="magnetic-stirrer-base">
              <div class="magnetic-stirrer-top"></div>
              <div class="indicator" id="indicator"></div>
              <div class="switch1"></div>
              <div class="switch2"></div>
            </div>
          </div>
          <Paper className={classes.paper1}>
            <Paper elevation={0}>
              <Button
                className="ml-auto mr-auto"
                id="start"
                variant="outlined"
                color="primary"
                onClick={(e) => {
                  start_titration();
                }}
              >
                Start
              </Button>
              <Button
                className="ml-auto mr-auto"
                id="stop"
                variant="outlined"
                color="danger"
                onClick={(e) => {
                  stop_titration();
                }}
              >
                Stop
              </Button>
              <br />
              <br />
              <Button
                variant="outlined"
                color="secondary"
                onClick={(e) => {
                  reset();
                }}
              >
                Reset
              </Button>
            </Paper>
          </Paper>
        </Grid>
        <Grid item xs={3}>
          <Paper className={classes.paper}>
            <Paper elevation={0}>
              <b>OBSERVATION:</b>
              <br />
              <br />
              <b>
                <u>End Point:</u>
              </b>
              <br />
              <br />
              <b>Colour Change:</b> Wine Red turns Blue
              <br />
              <br />
              <b>Volume of titrant used: </b>
              {current_volume_of_edta.toFixed(1)} ml
              <hr />
              <br />
              <br />
              <b>
                <u>RESULT:</u>
              </b>
              <br />
              <br />
              <b>Enter your answer:</b> <br />
              {/* <Popover
                id="mouse-over-popover"
                className={classes.popover}
                classes={{
                  paper: classes.paper,
                }}
                open={open}
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: "bottom",
                  horizontal: "left",
                }}
                transformOrigin={{
                  vertical: "top",
                  horizontal: "left",
                }}
                onClose={handlePopoverClose}
                disableRestoreFocus
              > */}
              Hardness of Water ={/* </Popover> */}
              <Input
                className={classes.input1}
                value={user_result}
                margin="dense"
                onChange={handleRChange}
                // onBlur={handleBlur}
                disabled={current_color !== "blue"}
                inputProps={{
                  type: "tel",
                }}
              />{" "}
              ppm
              <br />
              <br />
              <Button
                disabled={!user_result}
                variant="outlined"
                color="danger"
                onClick={(e) => {
                  check_result();
                }}
              >
                Submit
              </Button>
              {correct && (
                <Alert severity="success">Your answer is correct. Good!</Alert>
              )}
              {wrong && (
                <Alert severity="error">
                  Your answer is incorrect. Try Again!
                </Alert>
              )}
              <br />
              <hr />
              <br />
              {(correct || wrong) && (
                <div className="result">
                  <Button
                    variant="outlined"
                    color="danger"
                    onClick={(e) => {
                      calc_hardness();
                    }}
                  >
                    Show Result
                  </Button>
                  <br />
                  {result && <>{result} ppm</>}
                </div>
              )}
            </Paper>
          </Paper>
        </Grid>
      </Grid>
    </>
  );
}
